<template>
  <div class="my-5 mx-3 text-h5 font-weight-medium">
    {{ getNewEquipments.title }}
  </div>
  <v-divider></v-divider>
  <v-card elevation="0" class="mx-3 my-2 paragraph">
    <v-card-item>
      <v-card-title
        ><v-icon aria-hidden="false" size="x-small"> mdi-chevron-right </v-icon
        >新裝備<span class="ps-1 text-caption text-medium-emphasis"
          >New Equipment</span
        >
      </v-card-title>
    </v-card-item>
    <v-card-text class="px-0">
      <v-card elevation="0">
        <v-card-text>
          <div
            v-for="(line, index) in getNewEquipments.introduction"
            :key="index"
          >
            {{ line }}
          </div>
          <EquipmentCommentTabs
            class="mt-5"
            :equipmentCommentList="getNewEquipments.comments"
            :hideEmpty="true"
          />
        </v-card-text>
      </v-card>
    </v-card-text>
  </v-card>
</template>
<script>
import { mapActions, mapGetters } from 'vuex';
import EquipmentCommentTabs from '@/components/EquipmentCommentTabs.vue';
export default {
  name: 'NewEquipmentView',
  components: { EquipmentCommentTabs },
  data: () => ({}),
  props: [],
  computed: { ...mapGetters('misc', ['getNewEquipments']) },
  async created() {},
  async mounted() {
    await this.fetchNewEquipments();
    await this.updateLoadStatus(true);
  },
  async updated() {
    await this.updateLoadStatus(true);
  },
  methods: {
    ...mapActions(['updateLoadStatus']),
    ...mapActions('misc', ['fetchNewEquipments']),
  },
};
</script>
