<template>
  <div id="equipment">
    <v-card class="mx-0 px-0" elevation="0">
      <v-tabs
        id="tabs"
        :class="{
          sticky: stickyTabs,
          'desktop-sticky': stickyTabs && !isMobileMode,
          'mobile-sticky': stickyTabs && isMobileMode,
        }"
        class="mx-0"
        v-model="tab"
        color="secondary"
        center-active
        v-bind:show-arrow="!isMobileMode"
        v-bind:grow="!isMobileMode"
      >
        <template
          v-for="category in equipmentCommentList"
          :key="category.categoryCode"
        >
          <v-tab
            :value="category.categoryCode"
            v-show="
              !isMobileMode &&
              !(
                hideEmpty &&
                category.items.length == 0 &&
                category.cards.length == 0
              )
            "
            @click="tabChange()"
          >
            {{ category.category }}
          </v-tab>
        </template>
        <v-menu>
          <template v-slot:activator="{ props }">
            <div
              v-if="isMobileMode"
              class="align-self-center text-caption font-weight-light ms-5"
            >
              可選擇裝備部位換頁
              <v-icon>mdi-chevron-triple-right</v-icon>
            </div>
            <v-spacer v-if="isMobileMode"></v-spacer>
            <v-btn
              v-show="isMobileMode"
              variant="flat"
              rounded="0"
              class="align-self-center"
              height="100%"
              prepend-icon="fa:fa-solid fa-compass"
              v-bind="props"
            >
              {{ mobileEquipmentMenuTitle }}
              <v-icon end> mdi-menu-down </v-icon>
            </v-btn>
          </template>

          <v-list class="grey lighten-3">
            <template
              v-for="category in equipmentCommentList"
              :key="category.categoryCode"
            >
              <v-list-item
                v-if="
                  !(
                    hideEmpty &&
                    category.items.length == 0 &&
                    category.cards.length == 0
                  )
                "
                @click="tabChange(category.categoryCode)"
              >
                {{ category.category }}
              </v-list-item>
            </template>
          </v-list>
        </v-menu>
      </v-tabs>
      <v-card-text class="mx-0 px-0">
        <v-window v-model="tab">
          <keep-alive
            v-for="category in equipmentCommentList"
            :key="category.categoryCode"
          >
            <v-window-item
              reverse-transition="fade-transition"
              transition="fade-transition"
              eager
              v-if="
                !(
                  hideEmpty &&
                  category.items.length == 0 &&
                  category.cards.length == 0
                )
              "
              :value="category.categoryCode"
            >
              <v-card
                elevation="0"
                class="mx-0 px-0"
                v-if="!(hideEmpty && category.items.length == 0)"
              >
                <v-card-item
                  class="px-0"
                  :id="getRefName(category.categoryCode, false)"
                >
                  <v-card-title
                    class="text-subtitle-1"
                    v-if="category.overrideCategoryName"
                    ><v-icon aria-hidden="false" size="x-small">
                      mdi-chevron-double-right </v-icon
                    >{{ category.category }}</v-card-title
                  >
                  <v-card-title class="text-subtitle-1" v-else
                    ><v-icon aria-hidden="false" size="x-small">
                      mdi-chevron-double-right </v-icon
                    >{{ category.category }}裝備
                    <span class="ps-1 text-caption text-medium-emphasis"
                      >{{ category.categorySubTitle }} Equipment</span
                    >
                  </v-card-title>
                </v-card-item>
                <v-card-text class="px-0">
                  <EquipmentComment
                    v-for="item in category.items"
                    :key="item.id"
                    :item="item"
                    :categoryCode="category.categoryCode"
                    :isCard="false"
                    @openDialog="openDialog"
                  ></EquipmentComment>
                </v-card-text>
              </v-card>
              <v-card
                elevation="0"
                class="mx-0 px-0"
                v-if="
                  category.hasCard && !(hideEmpty && category.cards.length == 0)
                "
              >
                <v-card-item
                  class="px-0"
                  :id="getRefName(category.categoryCode, true)"
                >
                  <v-card-title
                    class="text-subtitle-1"
                    v-if="category.overrideCategoryName"
                    ><v-icon aria-hidden="false" size="x-small">
                      mdi-chevron-double-right </v-icon
                    >{{ category.category }}</v-card-title
                  >
                  <v-card-title class="text-subtitle-1" v-else
                    ><v-icon aria-hidden="false" size="x-small">
                      mdi-chevron-double-right </v-icon
                    >{{ category.category }}卡片
                    <span class="ps-1 text-caption text-medium-emphasis"
                      >{{ category.categorySubTitle }} Card</span
                    >
                  </v-card-title>
                </v-card-item>
                <v-card-text class="px-0">
                  <EquipmentComment
                    v-for="card in category.cards"
                    :key="card.id"
                    :item="card"
                    :categoryCode="category.categoryCode"
                    :isCard="true"
                    @openDialog="openDialog"
                  ></EquipmentComment>

                  <v-alert
                    v-if="category.hasCard && category.cards.length == 0"
                    density="comfortable"
                    type="info"
                    variant="tonal"
                  >
                    {{ category.noCardMessage }}
                  </v-alert>
                </v-card-text>
              </v-card>
            </v-window-item>
          </keep-alive>
        </v-window>
      </v-card-text>
    </v-card>

    <v-dialog v-model="dialog.open" transition="fade-transition" scrollable>
      <v-card>
        <v-card-title>
          {{ dialog.item.name }}
          {{ getSlotText(dialog.item.slots) }}
        </v-card-title>

        <v-divider></v-divider>
        <v-card-text class="pa-0">
          <template v-for="(info, index) in dialog.item.infos" :key="index">
            <v-card elevation="0">
              <v-card-text>
                <template v-for="(line, index) in info.contents" :key="index">
                  <div v-html="line" v-if="line.length > 0"></div>
                  <br v-else />
                </template>
                <br />
                <div>
                  <div>
                    <span v-if="!isSpecialCategory"
                      >系列：{{ info.class }}
                    </span>
                    <span
                      class="ms-5"
                      v-if="!isSpecialCategory && !dialog.isCard"
                    >
                      {{ getValueTitle(dialog.categoryCode) }}：{{ info.value }}
                    </span>
                  </div>
                  <div v-if="!isSpecialCategory">
                    <span v-if="info.position.length > 0"
                      >位置：{{ info.position }}
                    </span>
                    <span :class="info.position.length > 0 ? 'ms-5' : ''"
                      >重量：{{ info.weight }}</span
                    >
                  </div>
                  <div v-if="!isSpecialCategory && !dialog.isCard">
                    <span>要求等級：{{ info.requiresLevel }} </span>
                  </div>
                  <div v-if="!isSpecialCategory && !dialog.isCard">
                    <span>裝備等級：{{ info.equipmentLevel }} </span>
                  </div>
                  <div v-if="!isSpecialCategory && !dialog.isCard">
                    <span>裝備：{{ info.usableBy }} </span>
                  </div>
                </div>
                <template
                  v-if="
                    !isSpecialCategory &&
                    !dialog.isCard &&
                    info.enchantments.length > 0
                  "
                >
                  <v-divider class="my-3"></v-divider>
                  <div class="text-subtitle-2 font-weight-bold">附魔範例</div>
                  <ul class="main-list ms-4 text-secondary">
                    <li
                      v-for="(enchantment, index) in info.enchantments"
                      :key="index"
                    >
                      {{ enchantment.name }}
                      <ul class="sub-list ms-6 text-primary">
                        <li
                          v-for="(detail, indexDetail) in enchantment.detail"
                          :key="indexDetail"
                        >
                          {{ detail }}
                        </li>
                      </ul>
                    </li>
                  </ul>
                </template>
              </v-card-text>
            </v-card>
          </template>
        </v-card-text>

        <v-card-actions class="d-flex justify-end">
          <v-btn
            block
            size="small"
            color="primary"
            @click="dialog.open = false"
          >
            關閉
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import EquipmentComment from './EquipmentComment.vue';
import { mapGetters, mapActions } from 'vuex';
export default {
  name: 'EquipmentCommentTabs',
  data: () => ({
    tab: null,
    mobileEquipmentMenuTitle: '裝備部位',
    dialog: {
      open: false,
      item: {},
      categoryCode: '',
      isCard: false,
    },
    stickyTabs: false,
    stickyTriggerY: 0,
  }),
  props: ['equipmentCommentList', 'hideEmpty'],
  components: {
    EquipmentComment,
  },
  computed: {
    ...mapGetters(['isMobileMode']),
    isSpecialCategory() {
      return (
        this.dialog.categoryCode == 'costume' ||
        this.dialog.categoryCode == 'pet'
      );
    },
  },
  created() {},
  mounted() {
    window.addEventListener('scroll', this.handleScroll);
  },
  updated() {
    this.setDefaultTab();
  },
  methods: {
    ...mapActions(['updateLoadStatus']),
    setDefaultTab() {
      if (this.tab == null) {
        if (this.hideEmpty) {
          const defaultCategory = this.equipmentCommentList.find(
            (c) => c.items.length > 0 || c.cards.length > 0
          );
          this.tab = defaultCategory?.categoryCode;
          this.mobileEquipmentMenuTitle = defaultCategory?.category;
        } else {
          this.tab = this.equipmentCommentList[0]?.categoryCode;
          this.mobileEquipmentMenuTitle =
            this.equipmentCommentList[0]?.category;
        }
      }
    },
    getRefName(categoryCode, isCard) {
      let target = '';
      if (!isCard) {
        target = `${categoryCode}-equipment`;
      } else {
        target = `${categoryCode}-card`;
      }
      return target.replace(/-/gi, '_');
    },
    openDialog(item, categoryCode, isCard) {
      this.dialog.item = item;
      this.dialog.categoryCode = categoryCode;
      this.dialog.isCard = isCard;
      this.dialog.open = true;
    },
    getSlotText(slots) {
      let text = '';
      if (slots > 0) {
        text = `[${slots}]`;
      }
      return text;
    },
    getValueTitle(categoryCode) {
      let title = '';
      switch (categoryCode) {
        case 'weapon':
          title = '攻擊';
          break;

        default:
          title = '防禦';
          break;
      }
      return title;
    },
    handleScroll() {
      const offsetTop = document
        .getElementById('tabs')
        .getBoundingClientRect().y;
      if (offsetTop <= 50 && this.stickyTabs == false) {
        this.stickyTabs = true;
        this.stickyTriggerY = window.scrollY;
      }

      if (
        window.scrollY <= this.stickyTriggerY - 50 &&
        this.stickyTabs == true
      ) {
        this.stickyTabs = false;
      }
    },
    tabChange(categoryCode = '') {
      if (categoryCode != '') {
        this.tab = categoryCode;
      }
      const targetCategory = this.equipmentCommentList.find(
        (c) => c.categoryCode == this.tab
      );
      this.mobileEquipmentMenuTitle = targetCategory.category;
      const el = document.getElementById('equipment');
      this.$smoothScroll({
        scrollTo: el,
        duration: 300,
        offset: -80,
      });
    },
  },
};
</script>
<style scoped>
.btn-info {
  min-width: 50px !important;
  width: 50px !important;
}
.btn-info .icon {
  padding-bottom: 1px !important;
}
.rank {
  padding-top: 1px;
}

.sticky {
  position: fixed;
  top: 50px;
  z-index: 99;
  background-color: white;
}
.desktop-sticky {
  width: calc(100% - 310px);
}
.mobile-sticky {
  width: calc(100% - 55px);
}
#tabs {
  border-style: solid;
  border-width: 1px;
  border-radius: 3px;
  border-color: #cecece;
  opacity: 90%;
}

.v-tabs .v-btn.v-btn--density-default {
  height: initial;
}
</style>
