import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '@/views/HomeView.vue'
import GuideView from '@/views/GuideView.vue'
import AboutView from "@/views/AboutView.vue";
import NewEquipmentView from '@/views/misc/NewEquipmentView.vue'
import NewElementTableView from '@/views/misc/NewElementTableView.vue'
import store from '@/store/index.js'
const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/guide/:classType',
    name: 'guide',
    component: GuideView,
    props: true
  },
  {
    path: '/msic/new_equipment',
    name: 'new_equipment',
    component: NewEquipmentView,
  },
  {
    path: '/msic/new_element_table',
    name: 'new_element_table',
    component: NewElementTableView,
  },
  {
    path: '/contact/about',
    name: 'about',
    component: AboutView,
  },
  {
    path: "/:catchAll(.*)", redirect: '/'
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})
router.beforeEach((to) => {
  store.dispatch('updateLoadStatus', false);
  store.dispatch('updateFixedFooterStatus', to.name == 'home' || to.name == 'about' || to.name == 'new_equipment');
});
router.afterEach(() => {

})
export default router
