<template>
  <div class="my-5 mx-3 text-h5 font-weight-medium">
    {{ getGuide.title }}
  </div>
  <v-divider></v-divider>

  <!-- 介紹 Introduction -->
  <v-card elevation="0" class="mx-3 my-2 paragraph">
    <v-card-title>
      <v-icon aria-hidden="false" size="x-small" class=""
        >mdi-chevron-right </v-icon
      >{{ getGuide.introduction.title
      }}<span class="ps-1 text-caption text-medium-emphasis">{{
        getGuide.introduction.subTitle
      }}</span>
    </v-card-title>
    <v-card-text>
      <div v-for="(line, index) in getGuide.introduction.content" :key="index">
        {{ line }}
      </div>
    </v-card-text>
  </v-card>

  <!-- 配點 Build -->
  <v-card elevation="0" class="mx-3 my-2 paragraph">
    <v-card-title
      ><v-icon aria-hidden="false" size="x-small"> mdi-chevron-right </v-icon
      >{{ getGuide.build.title
      }}<span class="ps-1 text-caption text-medium-emphasis">{{
        getGuide.build.subTitle
      }}</span>
    </v-card-title>
    <v-card-text class="px-0">
      <v-card elevation="0">
        <v-card-title class="text-subtitle-1">
          <v-icon aria-hidden="false" size="x-small">
            mdi-chevron-double-right </v-icon
          >{{ getGuide.build.skill.title
          }}<span class="ps-1 text-caption text-medium-emphasis">
            {{ getGuide.build.skill.subTitle }}
          </span>
        </v-card-title>
        <v-card-text>
          <div
            v-for="(line, index) in getGuide.build.skill.content"
            :key="index"
          >
            {{ line }}
          </div>
          <SkillInfo
            v-for="(info, index) in getGuide.build.skill.infos"
            :info="info"
            :key="index"
          ></SkillInfo>
          <template class="d-flex flex-wrap">
            <PictureWithText
              v-for="(image, index) in skillImages"
              :url="getImageFullPath(image.url)"
              :description="image.description"
              :maxWidth="600"
              :key="index"
            />
          </template>
        </v-card-text>
      </v-card>

      <v-card elevation="0" class="mt-2">
        <v-card-title class="text-subtitle-1"
          ><v-icon aria-hidden="false" size="x-small">
            mdi-chevron-double-right </v-icon
          >{{ getGuide.build.state.title
          }}<span class="ps-1 text-caption text-medium-emphasis"
            >{{ getGuide.build.state.subTitle }}
          </span>
        </v-card-title>
        <v-card-text>
          <div
            v-for="(line, index) in getGuide.build.state.content"
            :key="index"
          >
            {{ line }}
          </div>

          <template class="d-flex flex-wrap">
            <PictureWithText
              v-for="(image, index) in stateImages"
              :url="getImageFullPath(image.url)"
              :description="image.description + getGuide.build.state.remark"
              :maxWidth="400"
              :key="index"
            />
          </template>
        </v-card-text>
      </v-card>
    </v-card-text>
  </v-card>

  <!-- 裝備 Equipment -->
  <v-card elevation="0" class="mx-3 my-2 paragraph">
    <v-card-title
      ><v-icon aria-hidden="false" size="x-small"> mdi-chevron-right </v-icon
      >{{ getGuide.equipment.title
      }}<span class="ps-1 text-caption text-medium-emphasis"
        >{{ getGuide.equipment.subTitle }}
      </span>
    </v-card-title>
    <v-card-text class="px-0">
      <v-card elevation="0">
        <v-card-text>
          <div v-for="(line, index) in getGuide.equipment.content" :key="index">
            {{ line }}
          </div>
          <template class="d-flex flex-wrap">
            <PictureWithText
              v-for="(image, index) in equipmentImages"
              :url="getImageFullPath(image.url)"
              :description="image.description"
              :maxWidth="400"
              :key="index"
            />
          </template>
          <EquipmentCommentTabs
            :equipmentCommentList="getGuide.equipment.comments"
          />
        </v-card-text>
      </v-card>
    </v-card-text>
  </v-card>

  <!-- 影片 Video -->
  <v-card elevation="0" class="mx-3 my-2 paragraph">
    <v-card-title
      ><v-icon aria-hidden="false"> mdi-chevron-right </v-icon
      >{{ getGuide.video.title
      }}<span class="ps-1 text-caption text-medium-emphasis">{{
        getGuide.video.subTitle
      }}</span>
    </v-card-title>
    <v-card-text>
      <div v-for="(line, index) in getGuide.video.content" :key="index">
        {{ line }}
      </div>
      <div
        v-for="(url, index) in getGuide.video.urls"
        :key="index"
        class="video-block ratio ratio-16x9"
      >
        <iframe
          width="560"
          height="315"
          :src="url"
          frameborder="0"
          allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
          allowfullscreen
        ></iframe>
      </div>
    </v-card-text>
  </v-card>
</template>
<script>
import EquipmentCommentTabs from '@/components/EquipmentCommentTabs.vue';
import SkillInfo from '@/components/SkillInfo.vue';
import PictureWithText from '@/components/PictureWithText.vue';
import { mapActions, mapGetters } from 'vuex';
export default {
  name: 'GuideView',
  components: {
    EquipmentCommentTabs,
    SkillInfo,
    PictureWithText,
  },
  data: () => ({
    skillImages: [
      {
        url: `skill_1.png`,
        description: '1轉技能配點',
      },
      {
        url: `skill_2.png`,
        description: '2轉技能配點',
      },
      {
        url: `skill_3.png`,
        description: '3轉技能配點',
      },
      {
        url: `skill_4.png`,
        description: '4轉技能配點',
      },
    ],
    stateImages: [
      {
        url: `state.png`,
        description: '素質配點',
      },
    ],
    equipmentImages: [
      {
        url: `equipment_1.png`,
        description: '一般裝備',
      },
      {
        url: `equipment_2.png`,
        description: '服飾、影子裝備',
      },
    ],
  }),
  props: ['classType'],
  computed: {
    ...mapGetters('guide', ['getGuide']),
  },
  async created() {},
  async mounted() {
    await this.loadGuideContent();
    await this.updateLoadStatus(true);
  },
  async updated() {
    await this.loadGuideContent();
    await this.updateLoadStatus(true);
  },
  methods: {
    ...mapActions(['updateLoadStatus']),
    ...mapActions('guide', ['fetchGuide']),
    getImageFullPath(fileName) {
      return `${process.env.BASE_URL}image/guide/${this.classType}/${fileName}`;
    },
    getBackgroundImageFullPath(fileName) {
      return `${process.env.BASE_URL}image/general/${fileName}`;
    },
    async loadGuideContent() {
      if (this.getGuide.classType != this.classType) {
        await Promise.all([this.fetchGuide(this.classType)]);
        this.backToTop();
      }
    },
    backToTop: function () {
      window.scrollTo({
        top: 0,
        behavior: 'smooth',
      });
    },
  },
};
</script>
<style scoped></style>
