<template>
  <v-card
    v-resize="onResize"
    elevation="0"
    :max-width="maxWidth"
    :width="maxWidth"
    class="my-3"
    :class="windowSize.x > maxWidth * 2 ? 'me-4' : ''"
  >
    <v-img :src="url" cover class="rounded"></v-img>
    <div class="ms-0 ps-0 mt-1 text-caption text-medium-emphasis">
      <v-icon class="icon">mdi-image-area</v-icon>&nbsp;{{ description }}
    </div>
  </v-card>
</template>
<script>
export default {
  name: 'PictureWithText',
  components: {},
  data: () => ({
    windowSize: {
      x: 0,
      y: 0,
    },
  }),
  props: ['url', 'description', 'maxWidth', 'minWidth'],
  computed: {},
  async created() {},
  mounted() {
    this.onResize();
  },
  methods: {
    onResize() {
      this.windowSize = { x: window.innerWidth, y: window.innerHeight };
    },
  },
};
</script>
<style scoped>
.icon {
  padding-bottom: 2px;
}
</style>
