import { createStore } from 'vuex'
import axios from "axios";
import guide from "./guide";
import misc from "./misc";

export default createStore({
  namespaced: true,
  state: {
    packageVersion: process.env.PACKAGE_VERSION || '0',
    isLoaded: false,
    pages: [],
    fixedFooter: false,
    shiftBackToTop: false,
    windowSize: {
      x: 0,
      y: 0
    }
  },
  getters: {
    appVersion: (state) => {
      return state.packageVersion
    },
    isMobileMode: (state) => {
      return state.windowSize.x < 1280;
    }
  },
  mutations: {
    setLoadStatus(state, payload) {
      state.isLoaded = payload;
    },
    setPages(state, payload) {
      state.pages = payload;
    },
    setFixedFooterStatus(state, payload) {
      state.fixedFooter = payload;
    },
    setShiftBackToTop(state, payload) {
      state.shiftBackToTop = payload;
    },
    setWindowSize(state, payload) {
      state.windowSize = payload;
    }
  },
  actions: {
    updateLoadStatus(context, isloaded) {
      context.commit('setLoadStatus', isloaded);
      if (process.env.NODE_ENV.trim() === 'development') {
        console.log(`isloaded:${isloaded}`);
      }
    },
    async fetchPages(context) {
      let pages = (await axios.get(`${process.env.BASE_URL}database/pageLink.json`)).data;
      pages.forEach(page => {
        page.subPages = page.subPages.filter(p => p.enable == true);
      });
      context.commit('setPages', pages);
    },
    updateFixedFooterStatus(context, enable) {
      context.commit('setFixedFooterStatus', enable);
    },
    updateShiftBackToTop(context, needShift) {
      context.commit('setShiftBackToTop', needShift);
    },
    updateWindowSize(context, windowSize) {
      context.commit('setWindowSize', windowSize);
    }
  },
  modules: {
    guide, misc
  }
})
