<template>
  <v-card elevation="0" class="my-2" color="#E5F3FE" max-width="800">
    <v-card-item>
      <v-card-title
        class="text-subtitle-2 font-weight-bold d-flex flex-row align-center"
        ><v-img
          class="me-1 pointer"
          :src="getIconSrc(info.icon)"
          max-height="24"
          max-width="24"
          height="24"
          width="24"
        ></v-img
        >{{ info.name }}</v-card-title
      >
    </v-card-item>
    <v-card-text>
      <div v-for="(line, index) in info.description" :key="index">
        {{ line }}
      </div>
      <br />
      <v-icon aria-hidden="false" class="pb-1">mdi-calculator-variant</v-icon>
      <span class="font-weight-bold">公式：</span>
      <div v-for="(line, index) in info.formula" :key="index">
        {{ line }}
      </div>
      <br />
      <div v-for="(line, index) in info.level" :key="index">
        <!-- <span class="font-weight-bold">[Lv. {{ index + 1 }}]</span> {{ line }} -->
        <span class="font-weight-bold">
          [ Lv. <v-icon class="icon-level">mdi-numeric-{{ index + 1 }}</v-icon
          >]
        </span>
        {{ line }}
      </div>
    </v-card-text>
  </v-card>
</template>
<script>
export default {
  name: 'SkillInfo',
  components: {},
  data: () => ({}),
  props: ['info'],
  computed: {},
  async created() {},
  async mounted() {},
  methods: {
    getIconSrc(name) {
      return `${process.env.BASE_URL}image/skill/icon/${name}`;
    },
  },
};
</script>
<style scoped>
.icon-level {
  padding-bottom: 2px;
}
</style>
