<template>
  <PageLoader />
  <v-layout>
    <v-app-bar color="primary" density="compact" prominent app>
      <v-img
        class="me-1 pointer"
        :src="require(`@/assets/image/logo_trans.png`)"
        max-height="30"
        max-width="30"
        height="30"
        width="30"
        v-on:click="goHome"
      ></v-img>
      <v-toolbar-title
        id="site-title"
        class="text-h6 font-weight-medium pointer"
        v-on:click="goHome"
      >
        Rein Forest
      </v-toolbar-title>

      <v-spacer></v-spacer>
      <v-btn
        href="https://stellvia2359.github.io/archive-site/index.html"
        target="_blank"
      >
        <v-icon class="me-1">mdi-archive</v-icon>
        <span v-if="isMobileMode">舊版</span>
        <span v-else>前往舊版網站</span>
      </v-btn>
      <v-app-bar-nav-icon
        variant="text"
        @click.stop="drawer = !drawer"
        class="text-start ms-0"
        v-if="isMobileMode"
      ></v-app-bar-nav-icon>
    </v-app-bar>

    <v-navigation-drawer app v-model="drawer">
      <v-list dense nav>
        <template v-for="(page, index) in pages" :key="index">
          <v-list-group>
            <template v-slot:activator="{ props }">
              <v-list-item
                v-bind="props"
                :prepend-icon="page.icon"
                :value="page.route"
                no-action
                class="my-0"
              >
                <v-list-item-header>
                  <v-list-item-title>
                    {{ page.title }}
                    <span class="text-caption ms-1">
                      {{ page.subTitle }}
                    </span>
                  </v-list-item-title>
                </v-list-item-header>
              </v-list-item>
            </template>
            <v-list-item
              v-for="(subPage, indexSub) in page.subPages"
              :key="indexSub"
              :to="subPage.isExternal ? '' : subPage.route"
              :value="subPage.route"
              :href="!subPage.isExternal ? '' : subPage.route"
              :target="subPage.isExternal ? '_blank' : '_self'"
              link
              class="mt-1"
            >
              <v-list-item-header>
                <v-list-item-title>
                  {{ subPage.title }}
                  <span class="text-caption ms-1">
                    {{ subPage.subTitle }}
                  </span>
                </v-list-item-title>
              </v-list-item-header>
            </v-list-item>
          </v-list-group>
          <v-divider class="my-1"></v-divider>
        </template>
      </v-list>
    </v-navigation-drawer>
    <v-main v-resize="onResize">
      <router-view />
      <v-footer bottom class="pa-0 ma-0 footer" :class="{ fixed: fixedFooter }">
        <v-card
          elevation="0"
          rounded="0"
          width="100%"
          class="pa-0"
          color="#e9ebec"
        >
          <v-card-text class="py-1 px-4">
            <div>
              <span>Copyright</span>
              <v-icon size="small" class="copyright mx-1">mdi-copyright</v-icon>
              <span>{{ new Date().getFullYear() }} — Rein Forest</span>
              <span class="ms-1 text-caption">v{{ appVersion }}</span>
            </div>
            <div>
              <span>Copyright</span>
              <v-icon size="small" class="copyright mx-1">mdi-copyright</v-icon>
              <span>GRAVITY Co,.Ltd & Lee MyoungJin(studio DTDS).</span>
            </div>
          </v-card-text>
        </v-card>
      </v-footer>
      <v-fade-transition>
        <v-btn
          v-if="showBackToTop"
          size="x-large"
          :class="shiftBackToTop ? 'btn-back-to-top-shift' : 'btn-back-to-top'"
          icon="mdi-chevron-up"
          color="primary"
          @click="backToTop"
        >
        </v-btn>
      </v-fade-transition>
    </v-main>
  </v-layout>
</template>
<script>
import PageLoader from './components/PageLoader';
import { mapActions, mapGetters, mapState } from 'vuex';
export default {
  data: () => ({
    drawer: true,
    showBackToTop: false,
  }),
  props: [],
  components: {
    PageLoader,
  },
  computed: {
    ...mapState(['pages', 'fixedFooter', 'shiftBackToTop']),
    ...mapGetters(['appVersion', 'isMobileMode']),
    scrollbarTheme() {
      return this.$vuetify.theme.dark ? 'dark' : 'light';
    },
    buttonText() {
      return !this.$vuetify.theme.dark ? 'Switch to dark' : 'Switch to light';
    },
  },
  async created() {
    await this.fetchPages();
  },
  mounted() {
    this.$loadScript('https://cdn.jsdelivr.net/npm/@widgetbot/crate@3')
      .then(() => {
        // eslint-disable-next-line no-undef
        this.crate = new Crate({
          server: '689104053690433546',
          channel: '689104054164783247',
          color: 'rgba(52, 58, 64, 0.8)',
          glyph: [process.env.BASE_URL + 'image/logo_trans.png', '53.5px'],
        });
        this.crate.notify({
          content: '`有問題歡迎討論，若無法使用可直接至Discord頻道留言喔。`',
          timeout: 3000,
        });
      })
      .catch(() => {
        // Failed to fetch script
      });
    window.addEventListener('scroll', this.handleScroll);
  },
  unmounted() {
    this.crate.hide();
  },
  methods: {
    ...mapActions(['fetchPages', 'updateWindowSize']),
    goHome() {
      this.selection = [];
      this.$router.push('/');
    },
    handleScroll: function () {
      this.showBackToTop = window.scrollY > 50;
    },
    backToTop: function () {
      window.scrollTo({
        top: 0,
        behavior: 'smooth',
      });
    },
    onResize() {
      this.updateWindowSize({ x: window.innerWidth, y: window.innerHeight });
    },
  },
};
</script>

<style lang="scss">
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@100;300;400;500;700;900&family=Rajdhani:wght@300;400;500;600;700&display=swap');

@import '~vuetify/lib/styles/settings/_variables.scss';

@font-face {
  font-family: My-Rajdhani;
  src: url(assets/fonts/Rajdhani/Rajdhani-Medium.ttf);
}
@font-face {
  font-family: My-Rajdhani;
  src: url(assets/fonts/Rajdhani/Rajdhani-Light.ttf);
  font-weight: 200;
}
@font-face {
  font-family: My-Rajdhani;
  src: url(assets/fonts/Rajdhani/Rajdhani-Regular.ttf);
  font-weight: 300;
}
@font-face {
  font-family: My-Rajdhani;
  src: url(assets/fonts/Rajdhani/Rajdhani-SemiBold.ttf);
  font-weight: 500;
}
@font-face {
  font-family: My-Rajdhani;
  src: url(assets/fonts/Rajdhani/Rajdhani-Bold.ttf);
  font-weight: 600;
}

$font-family: 'Rajdhani', 'Noto Sans TC', sans-serif;
$font-family-card: 'My-Rajdhani', 'Noto Sans TC', sans-serif;
#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  font-size: 0.875rem;

  [class*='text-'] {
    font-family: $font-family !important;
  }
  font-family: $font-family !important;
}
.v-overlay-container {
  font-family: $font-family !important;
  font-weight: 600 !important;
}
.v-card-text {
  font-family: $font-family-card !important;
}
body {
  background-color: #fafafa !important;
}
.body-color {
  background-color: #fafafa !important;
}

.pointer {
  cursor: pointer;
}

/*scroll bar*/
::-webkit-scrollbar {
  width: 15px;
}

::-webkit-scrollbar-track {
  background: #e6e6e6;
  border-left: 1px solid #dadada;
}

::-webkit-scrollbar-thumb {
  background: #b0b0b0;
  border: solid 3px #e6e6e6;
  border-radius: 7px;
}

::-webkit-scrollbar-thumb:hover {
  background: black;
}
#site-title {
  max-width: 145px;
}
.v-list-item-icon {
  margin-right: 10px;
}
.v-list-group__items {
  --parent-padding: 15px;
}
.v-list-group__items .v-list-item--active {
  color: #2196f3;
}
/*
.v-list-group__items .v-list-item {
  padding-left: 8px !important;
}
*/

.video-block {
  max-width: 750px;
  margin-bottom: 10px;
}
.ratio {
  position: relative;
  width: 100%;
}

.ratio::before {
  display: block;
  padding-top: var(--bs-aspect-ratio);
  content: '';
}

.ratio > * {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.ratio-1x1 {
  --bs-aspect-ratio: 100%;
}

.ratio-4x3 {
  --bs-aspect-ratio: 75%;
}

.ratio-16x9 {
  --bs-aspect-ratio: 56.25%;
}

.ratio-21x9 {
  --bs-aspect-ratio: 42.8571428571%;
}
.footer {
  width: 100%;
  bottom: 0;
}
.copyright {
  padding-bottom: 2px;
}

.fixed {
  position: fixed;
}

.btn-back-to-top-shift {
  position: fixed;
  bottom: 18px;
  right: 150px;
  z-index: 99;
  padding: 0;
  opacity: 70%;
  transform: translate3d(0, 0, 200px);
}
.btn-back-to-top {
  position: fixed;
  bottom: 18px;
  right: 85px;
  z-index: 99;
  padding: 0;
  opacity: 70%;
  transform: translate3d(0, 0, 200px);
}

@media (max-width: 500px) {
  .btn-back-to-top-shift {
    bottom: 2px;
    right: 121px;
  }
  .btn-back-to-top {
    bottom: 2px;
    right: 62px;
  }
}
</style>
